/** ÂèñË∑ØÂæÑÊúÄÂêé‰∏ÄÊÆµÔºöa/b/c -> c */
export function lastSegment(path: string) {
  return (path || '').split('/').filter(Boolean).pop() || path || '';
}

/* ------------------------------------------------------------------ */
/*  ‰ªªÂä°Ë°åÔºöÊ®°ÊùøÊ∏≤Êüì                                                   */
/* ------------------------------------------------------------------ */
export function makeTaskLine(opts: {
  themePath?: string;
  template?: string;                         // ‰æãÂ¶Ç "{{‰ªªÂä°Áä∂ÊÄÅ}} {{icon}} {{ÂÜÖÂÆπ}} ..."
  fields: Record<string, string | number | boolean | null | undefined>;
}): string {
  const { template = '{{ÂâçÁºÄ}}{{ÂÜÖÂÆπ}}', fields } = opts;

  /** Â≠óÊÆµÂÆâÂÖ®Âåñ */
  const safe: Record<string, string> = {};
  Object.entries(fields || {}).forEach(([k, v]) => { safe[k] = v == null ? '' : String(v); });
  if (!('ÂâçÁºÄ' in safe))      safe['ÂâçÁºÄ'] = '';
  if (!('‰ªªÂä°ÂâçÁºÄ' in safe))  safe['‰ªªÂä°ÂâçÁºÄ'] = safe['ÂâçÁºÄ'];

  /** ÊõøÊç¢Âç†‰ΩçÁ¨¶ÔºöÊîØÊåÅ {{key}} Âíå {{@key}} */
  const PLACEHOLDER = /\{\{\s*@?([^}]+?)\s*\}\}/g;
  let line = template.replace(PLACEHOLDER, (_m, p1) => {
    const key = String(p1).trim();
    return key in safe ? safe[key] : '';
  });

  /** Ëã•Ê®°ÊùøÊ≤°Êúâ‰ªª‰ΩïÂâçÁºÄÂç†‰ΩçÁ¨¶ÔºåÂàôÂú®ÊúÄÂâçÈù¢Ë°•‰∏Ä‰∏™ */
  const hasPrefixSlot = /\{\{\s*@?(?:ÂâçÁºÄ|‰ªªÂä°ÂâçÁºÄ)\s*\}\}/.test(template);
  if (!hasPrefixSlot && !line.startsWith('- [')) {
    const fallback = safe['ÂâçÁºÄ'] || '- [ ] ';
    line = fallback + line;
  }

  /** ÂéãÁº©Â§ö‰ΩôÁ©∫Ê†ºÂπ∂ËøîÂõû */
  return line.replace(/[ \t]+/g, ' ').trim();
}

/* ------------------------------------------------------------------ */
/*  ÂùóÔºöËÆ°Âàí / ÊÄªÁªì / ÊÄùËÄÉ / ÊâìÂç°                                       */
/* ------------------------------------------------------------------ */
export function makeBlock(opts: {
  category: string;                          // ËÆ°Âàí / ÊÄªÁªì / ÊÄùËÄÉ / ÊâìÂç°
  dateISO?: string;
  themeLabel?: string;                       // ‰∏ªÈ¢ò:: È°∂Â±ÇÊòæÁ§∫
  icon?: string;
  content: string;                           // ÂèØÂ§öË°å
  tags?: string[];                           // Ê†áÁ≠æ:: a, b
  extra?: Record<string,string>;             // Âë®Êúü / ÂàÜÁ±ª ...
  fieldsOrder?: string[];                    // Ëá™ÂÆö‰πâÂ≠óÊÆµÈ°∫Â∫è
}) {
  /* ÈªòËÆ§È°∫Â∫èÔºõÂèØÈÄöËøá fieldsOrder Ë¶ÜÁõñ */
  const order = opts.fieldsOrder ?? ['ÂàÜÁ±ª','Êó•Êúü','‰∏ªÈ¢ò','ÂõæÊ†á','Ê†áÁ≠æ','Âë®Êúü','ÂàÜÁ±ª','ÂÜÖÂÆπ'];

  /** ÁªÑË£ÖÊâÄÊúâÂèØËÉΩÂá∫Áé∞ÁöÑÂ≠óÊÆµ */
  const dict: Record<string,string> = {
    'ÂàÜÁ±ª': opts.category,
    'Êó•Êúü': opts.dateISO ? normalizeDateStr(opts.dateISO) : '',
    '‰∏ªÈ¢ò': opts.themeLabel ?? '',
    'ÂõæÊ†á': opts.icon ?? '',
    'Ê†áÁ≠æ': opts.tags?.join(', ') ?? '',
    ...opts.extra,                            // Âë®Êúü / ÂàÜÁ±ª Á≠â
    'ÂÜÖÂÆπ': (opts.content || '').trim()
  };

  /** helper */
  const kv = (k:string,v?:string)=> v ? `${k}:: ${v}` : '';

  const lines: string[] = ['<!-- start -->'];

  /* ÊåâÈ°∫Â∫èËæìÂá∫ */
  order.forEach(f => {
    if (f==='ÂÜÖÂÆπ' && dict['ÂÜÖÂÆπ'].includes('\n')) {
      const [first,...rest] = dict['ÂÜÖÂÆπ'].split('\n');
      lines.push('ÂÜÖÂÆπ:: '+ first, ...rest);
    } else {
      const line = kv(f, dict[f]);
      if (line) lines.push(line);
    }
  });

  lines.push('<!-- end -->');
  return lines.join('\n');
}

/* ------------------------------------------------------------------ */
/*  ÊóßÁâà‰ªªÂä°Ë°åÔºàÂ∏¶ emoji & #Ê†áÁ≠æÔºâ                                     */
/* ------------------------------------------------------------------ */
export function makeTaskLineLegacy(opts: {
  prefix: '- [ ] ' | '- [x] ' | '- [-] ' | '';
  title: string;
  themePath?: string;
  icon?: string;
  due?: string;
  start?: string;
  scheduled?: string;
  createdISO?: string;
  timeHHMM?: string;
  duration?: string | number;
  repeat?: 'day' | 'week' | 'month' | 'year' | null;
  markDate?: string;
  statusEmoji?: string;
}) {
  const tagStr  = opts.themePath ? ` #${opts.themePath}` : '';
  const iconStr = opts.icon ? `${opts.icon} ` : '';
  const pieces: string[] = [];

  pieces.push(`${opts.prefix}${iconStr}${opts.title}${tagStr}`.trim());

  if (opts.timeHHMM) pieces.push(`(Êó∂Èó¥::${opts.timeHHMM})`);
  if (opts.duration !== undefined && opts.duration !== '')
    pieces.push(`(Êó∂Èïø::${String(opts.duration)})`);

  const addDate = (emoji: string, d?: string) => {
    if (d) pieces.push(`${emoji} ${normalizeDateStr(d)}`);
  };
  addDate(EMOJI.created,   opts.createdISO);
  addDate(EMOJI.start,     opts.start);
  addDate(EMOJI.scheduled, opts.scheduled);
  addDate(EMOJI.due,       opts.due);

  if (opts.repeat)
    pieces.push(`${EMOJI.repeat} every ${opts.repeat}${opts.repeat === 'day' ? '' : 's'}`);

  if (opts.statusEmoji && opts.markDate)
    pieces.push(`${opts.statusEmoji} ${normalizeDateStr(opts.markDate)}`);

  return pieces.join(' ').trim();
}