// src/ui/InputSettingsTable.tsx
/** @jsxImportSource preact */
// InputSettingsTable.tsx ‚Äî‚Äî Á¥ßÂáëË°®Ê†º UIÔºàÂçïÂáªÂçïÂÖÉÊ†ºÂèØÁºñËæë JSON/ÂõæÊ†á + ÂèØÊäòÂè†Ôºâ

import { useState, useMemo } from 'preact/hooks';
import {
  Box, Table, TableHead, TableRow, TableCell, TableBody,
  IconButton, Tooltip, Dialog, DialogTitle, DialogContent,
  TextField, DialogActions, Button, Accordion, AccordionSummary, AccordionDetails
} from '@mui/material';
import AddIcon       from '@mui/icons-material/Add';
import SaveIcon      from '@mui/icons-material/Save';
import DeleteIcon    from '@mui/icons-material/Delete';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ThinkPlugin from '../main';
import { Notice } from 'obsidian';

/* ---------- ÂçïÂÖÉÊ†ºÊ†áËÆ∞Ôºà‰ªÖÊòæÁ§∫‰∏Ä‰∏™ÂõæÊ†áÔºâ ---------- */
const ENABLE_TEXT   = '‚úÖ';
const DISABLE_TEXT  = '‚ùå';
const INHERIT_TEXT  = 'üîΩ';
const OVERRIDE_TEXT = 'üìÑ';

/* Ë°åÈó¥Ë∑ùÔºàË°®Ê†ºÊõ¥Á¥ßÂáëÔºâ */
const ROW_PADDING_Y = 0.5; // 0.5 √ó theme.spacing = 4px
const CELL_PADDING_X = 1;  // 1 √ó theme.spacing  = 8px

/* ---------- ‰∏ªÁªÑ‰ª∂ ---------- */
interface Props { plugin: ThinkPlugin }
export function InputSettingsTable({ plugin }: Props) {
  const [refresh, setRefresh] = useState(0);

  /* ---------- ÂéüÂßãÊï∞ÊçÆ ---------- */
  const data = useMemo(() => {
    const raw = plugin.inputSettings || { base: {}, themes: [] };
    raw.base   ??= {};
    raw.themes ??= [];
    return raw;
  }, [plugin.inputSettings, refresh]);

  /* ---------- ÊäòÂè†Áä∂ÊÄÅÔºàÊú¨Âú∞ËÆ∞ÂøÜÔºâ ---------- */
  const [open, setOpen] = useState<boolean>(() => {
    const v = localStorage.getItem('think-input-settings-expanded');
    return v === null ? true : v === 'true';
  });
  const onToggle = (_: any, expanded: boolean) => {
    setOpen(expanded);
    try { localStorage.setItem('think-input-settings-expanded', String(expanded)); } catch {}
  };

  /* ---------- Âä®ÊÄÅÂàó ---------- */
  const blockKeys = useMemo(() => {
    const s = new Set<string>();
    data.themes.forEach((t: any) => Object.keys(t.blocks ?? {}).forEach(k => s.add(k)));
    Object.keys(data.base.blocks ?? {}).forEach(k => s.add(k));
    return Array.from(s).sort();
  }, [data]);

  /* ---------- ÂØπËØùÊ°ÜÁä∂ÊÄÅ ---------- */
  const [editing, setEditing] = useState<{
    themeIdx: number; type: string; json: string;
  } | null>(null);

  const [addOpen, setAddOpen] = useState(false);
  const [newPath, setNewPath] = useState('');

  const [delIdx,  setDelIdx]  = useState<number | null>(null);

  // ÂõæÊ†áÁºñËæëÂØπËØùÊ°Ü
  const [iconEdit, setIconEdit] = useState<{ themeIdx: number; value: string } | null>(null);

  /* ---------- JSON ÁºñËæë ---------- */
  const openEdit = (themeIdx: number, type: string) => {
    const obj = themeIdx < 0
      ? (type === 'task' ? data.base.task ?? {} : data.base.blocks?.[type] ?? {})
      : (type === 'task' ? data.themes[themeIdx].task ?? {} : data.themes[themeIdx].blocks?.[type] ?? {});
    setEditing({ themeIdx, type, json: JSON.stringify(obj, null, 2) });
  };

  const saveEdit = () => {
    if (!editing) return;
    let obj;
    try { obj = JSON.parse(editing.json || '{}'); }
    catch { new Notice('JSON Ëß£ÊûêÂ§±Ë¥•'); return; }

    if (editing.themeIdx < 0) {
      if (editing.type === 'task') data.base.task = obj;
      else {
        data.base.blocks ??= {};
        data.base.blocks[editing.type] = obj;
      }
    } else {
      const th = data.themes[editing.themeIdx];
      if (editing.type === 'task') th.task = obj;
      else {
        th.blocks ??= {};
        th.blocks[editing.type] = obj;
      }
    }
    plugin.inputSettings = data;
    plugin.persistAll().then(() => {
      new Notice('Â∑≤‰øùÂ≠òËÆæÁΩÆ');
      setEditing(null);
      setRefresh(r => r + 1);
    });
  };

  /* ---------- Êñ∞Â¢û / Âà†Èô§‰∏ªÈ¢ò ---------- */
  const confirmAdd = () => {
    const path = newPath.trim();
    if (!path) return;
    if (data.themes.some((t: any) => t.path === path)) { new Notice('ËØ•‰∏ªÈ¢òÂ∑≤Â≠òÂú®'); return; }
    data.themes.push({ path });
    plugin.inputSettings = data;
    plugin.persistAll().then(() => {
      new Notice('Â∑≤Êñ∞Â¢û‰∏ªÈ¢ò');
      setAddOpen(false); setNewPath(''); setRefresh(r => r + 1);
    });
  };

  const confirmDelete = () => {
    if (delIdx === null) return;
    const removed = data.themes.splice(delIdx, 1)[0];
    plugin.inputSettings = data;
    plugin.persistAll().then(() => {
      new Notice(`Â∑≤Âà†Èô§‰∏ªÈ¢ò„Äå${removed.path}„Äç`);
      setDelIdx(null); setRefresh(r => r + 1);
    });
  };

  /* ---------- Ê∏≤ÊüìÂ∑•ÂÖ∑ÔºöÂçïÂõæÊ†áÈÄªËæë ----------
     ËßÑÂàôÔºö
     - enabled === false  => ‚ùåÔºàÁ¶ÅÁî®Ôºâ
     - enabled !== false ‰∏î inherited === true  => üîΩÔºàÁªßÊâøÔºâ
     - enabled !== false ‰∏î inherited === false => üìÑÔºàË¶ÜÂÜôÔºâ
     Â§áÊ≥®ÔºöÂêØÁî®‰ΩÜÁªßÊâø/Ë¶ÜÂÜôÂà§ÂÆöÂêéÔºå‰∏çÂÜçÈ¢ùÂ§ñÊòæÁ§∫‚ÄúÂêØÁî®‚ÄùÂõæÊ†áÔºåÈÅøÂÖçÂÜó‰Ωô„ÄÇ
  ------------------------------------------------ */
  const renderCell = (cfg: any, inherited: boolean, themeIdx: number, type: string) => {
    const enabled = cfg?.enabled ?? true;
    const symbol  = enabled ? (inherited ? INHERIT_TEXT : OVERRIDE_TEXT) : DISABLE_TEXT;
    const tip     = !enabled ? 'Á¶ÅÁî®' : (inherited ? 'ÁªßÊâø' : 'Ë¶ÜÂÜô');

    return (
      <TableCell
        sx={{ cursor: 'pointer', whiteSpace: 'nowrap', textAlign: 'center', py: ROW_PADDING_Y, px: CELL_PADDING_X }}
        onClick={() => openEdit(themeIdx, type)}
        title={`ÂçïÂáªÁºñËæë / Á≤òË¥¥ JSONÔºàÂΩìÂâçÔºö${tip}Ôºâ`}
      >
        {symbol}
      </TableCell>
    );
  };

  const getCfg = (themeIdx: number, type: string) => {
    if (themeIdx < 0) return [data.base.task ?? {}, false] as [any, boolean];
    const th     = data.themes[themeIdx];
    const child  = type === 'task' ? th.task : th.blocks?.[type];
    const parent = type === 'task' ? data.base.task : data.base.blocks?.[type];
    const inh    = !child || Object.keys(child).length === 0;
    const cfg    = child ?? parent ?? {};
    return [cfg, inh] as [any, boolean];
  };

  /* ---------- ÁªÑ‰ª∂Ê∏≤Êüì ---------- */
  return (
    <Box sx={{ mt: 2 }}>
      <Accordion expanded={open} onChange={onToggle} disableGutters>
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <Box sx={{ display: 'flex', alignItems: 'center', width: '100%', justifyContent: 'space-between' }}>
            <strong style={{ fontSize: '1.1rem' }}>ÈÄöÁî®ËæìÂÖ•ËÆæÁΩÆÔºàÂçïÂáªÂçïÂÖÉÊ†ºÁºñËæëÔºâ</strong>
            <Tooltip title="Êñ∞Â¢û‰∏ªÈ¢ò">
              <IconButton size="small" onClick={(e) => { e.stopPropagation(); setAddOpen(true); }}>
                <AddIcon />
              </IconButton>
            </Tooltip>
          </Box>
        </AccordionSummary>

        <AccordionDetails>
          {/* ‰∏ªË°®Ê†ºÔºàË°®Â§¥‰∏çÊç¢Ë°åÔºâ */}
          <Table size="small" sx={{ '& th, & td': { whiteSpace: 'nowrap', py: ROW_PADDING_Y, px: CELL_PADDING_X } }}>
            <TableHead>
              <TableRow>
                <TableCell>‰∏ªÈ¢òË∑ØÂæÑ</TableCell>
                <TableCell align="center">ÂõæÊ†á</TableCell>
                <TableCell align="center">Task</TableCell>
                {blockKeys.map(k => (
                  <TableCell key={k} align="center">{k}</TableCell>
                ))}
                <TableCell /> {/* Êìç‰ΩúÂàó */}
              </TableRow>
            </TableHead>
            <TableBody>
              {/* Base Ë°å */}
              <TableRow sx={{ bgcolor: '#f7f7f7', '& > *': { py: ROW_PADDING_Y, px: CELL_PADDING_X } }}>
                <TableCell>
                  <strong>BaseÔºàÂÖ±ÊÄßÈªòËÆ§Ôºâ</strong>
                </TableCell>
                <TableCell align="center" />
                {renderCell(data.base.task ?? {}, false, -1, 'task')}
                {blockKeys.map(k => renderCell(data.base.blocks?.[k] ?? {}, false, -1, k))}
                <TableCell />
              </TableRow>

              {/* Theme Ë°å */}
              {data.themes.map((th: any, idx: number) => (
                <TableRow key={th.path} sx={{ '& > *': { py: ROW_PADDING_Y, px: CELL_PADDING_X } }}>
                  <TableCell>{th.path}</TableCell>

                  {/* ÂõæÊ†áÂçïÂÖÉÊ†º */}
                  <TableCell
                    align="center"
                    sx={{ cursor: 'pointer' }}
                    title="ÂçïÂáªÁºñËæëÂõæÊ†áÔºàÂèØËæìÂÖ•ÊñáÂ≠óÊàñË°®ÊÉÖÔºõÁïôÁ©∫=‰∏çÊòæÁ§∫Ôºâ"
                    onClick={() => setIconEdit({ themeIdx: idx, value: th.icon ?? '' })}
                  >
                    {th.icon ?? ''}
                  </TableCell>

                  {/* Task */}
                  {(() => {
                    const [cfg, inh] = getCfg(idx, 'task');
                    return renderCell(cfg, inh, idx, 'task');
                  })()}

                  {/* Blocks */}
                  {blockKeys.map(k => {
                    const child = th.blocks?.[k];
                    const inh   = !child || Object.keys(child).length === 0;
                    const cfg   = child ?? data.base.blocks?.[k] ?? {};
                    return renderCell(cfg, inh, idx, k);
                  })}

                  {/* Âà†Èô§‰∏ªÈ¢ò */}
                  <TableCell align="center">
                    <Tooltip title="Âà†Èô§‰∏ªÈ¢ò">
                      {/* Áõ¥Êé•‰ΩøÁî®ÂõæÊ†áÔºå‰∏ç‰ΩøÁî®ÂúÜÂΩ¢ÊåâÈíÆ */}
                      <DeleteIcon
                        sx={{ cursor: 'pointer', color: 'error.main' }}
                        onClick={() => setDelIdx(idx)}
                      />
                    </Tooltip>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </AccordionDetails>
      </Accordion>

      {/* JSON ÁºñËæëÂØπËØùÊ°Ü */}
      <Dialog open={!!editing} fullWidth maxWidth="sm" disablePortal onClose={() => setEditing(null)}>
        {editing && [
          <DialogTitle key="t">ÁºñËæëÈÖçÁΩÆÔºà{editing.themeIdx < 0 ? 'Base' : data.themes[editing.themeIdx].path} ‚Üí {editing.type}Ôºâ</DialogTitle>,
          <DialogContent key="c">
            <TextField
              multiline minRows={12} fullWidth
              value={editing.json}
              onChange={e => setEditing(p => p ? { ...p, json: (e.target as HTMLInputElement).value } : p)}
              onKeyDown={e => e.stopPropagation()}
              sx={{ fontFamily: 'monospace' }}
            />
          </DialogContent>,
          <DialogActions key="a">
            <Button onClick={() => setEditing(null)}>ÂèñÊ∂à</Button>
            <Button startIcon={<SaveIcon />} onClick={saveEdit}>‰øùÂ≠ò</Button>
          </DialogActions>,
        ]}
      </Dialog>

      {/* ÂõæÊ†áÁºñËæëÂØπËØùÊ°Ü */}
      <Dialog open={!!iconEdit} maxWidth="xs" fullWidth disablePortal onClose={() => setIconEdit(null)}>
        {iconEdit && [
          <DialogTitle key="t">ÁºñËæëÂõæÊ†áÔºà{data.themes[iconEdit.themeIdx].path}Ôºâ</DialogTitle>,
          <DialogContent key="c">
            <TextField
              autoFocus
              fullWidth
              placeholder="ÂèØËæìÂÖ•ÊñáÂ≠óÊàñË°®ÊÉÖÔºåÂ¶Ç ‚ú® / üò¥ / üí™"
              value={iconEdit.value}
              onChange={e => setIconEdit(p => p ? ({ ...p, value: (e.target as HTMLInputElement).value }) : p)}
              onKeyDown={e => e.stopPropagation()}
            />
          </DialogContent>,
          <DialogActions key="a">
            <Button onClick={() => setIconEdit(null)}>ÂèñÊ∂à</Button>
            <Button
              startIcon={<SaveIcon />}
              onClick={() => {
                const v = (iconEdit.value || '').trim();
                data.themes[iconEdit.themeIdx].icon = v || undefined;
                plugin.inputSettings = data;
                plugin.persistAll().then(() => {
                  new Notice('Â∑≤‰øùÂ≠òÂõæÊ†á');
                  setIconEdit(null);
                  setRefresh(r => r + 1);
                });
              }}
            >
              ‰øùÂ≠ò
            </Button>
          </DialogActions>,
        ]}
      </Dialog>

      {/* Êñ∞Â¢û‰∏ªÈ¢òÂØπËØùÊ°Ü */}
      <Dialog open={addOpen} maxWidth="xs" fullWidth disablePortal onClose={() => setAddOpen(false)}>
        {[
          <DialogTitle key="t">Êñ∞Â¢û‰∏ªÈ¢òË∑ØÂæÑ</DialogTitle>,
          <DialogContent key="c">
            <TextField
              fullWidth autoFocus
              placeholder="Â¶Ç ÂÅ•Â∫∑/È•ÆÈ£ü"
              value={newPath}
              onChange={e => setNewPath((e.target as HTMLInputElement).value)}
              onKeyDown={e => {
                e.stopPropagation();
                if ((e as any).key === 'Enter') {
                  e.preventDefault();
                  confirmAdd();
                }
              }}
            />
          </DialogContent>,
          <DialogActions key="a">
            <Button onClick={() => setAddOpen(false)}>ÂèñÊ∂à</Button>
            <Button onClick={confirmAdd}>Ê∑ªÂä†</Button>
          </DialogActions>,
        ]}
      </Dialog>

      {/* Âà†Èô§Á°ÆËÆ§ÂØπËØùÊ°Ü */}
      <Dialog open={delIdx !== null} maxWidth="xs" disablePortal onClose={() => setDelIdx(null)}>
        {delIdx !== null && [
          <DialogTitle key="t">Á°ÆËÆ§Âà†Èô§</DialogTitle>,
          <DialogContent key="c">
            Á°ÆËÆ§Âà†Èô§‰∏ªÈ¢ò„Äå{data.themes[delIdx].path}„ÄçÔºü
          </DialogContent>,
          <DialogActions key="a">
            <Button onClick={() => setDelIdx(null)}>ÂèñÊ∂à</Button>
            <Button color="error" onClick={confirmDelete}>Âà†Èô§</Button>
          </DialogActions>,
        ]}
      </Dialog>
    </Box>
  );
}
