/** @jsxImportSource preact */
import { h } from 'preact';
import { useState, useEffect } from 'preact/hooks';
import { DataStore } from '../data/store';
import { DashboardConfig, ModuleConfig } from '../config/schema';
import { ModulePanel } from './ModulePanel';
import type ThinkPlugin from '../main';
import { TFile, TFolder } from 'obsidian';
import { ViewComponents } from './index';
import { getDateRange, dayjs } from '../utils/date';         // (#5)
import {
  filterByRules,
  sortItems,
  filterByDateRange,
  filterByKeyword,
} from '../utils/itemFilter';

const QTXT = ['‰∏Ä', '‰∫å', '‰∏â', 'Âõõ'];

interface Props {
  config: DashboardConfig;
  dataStore: DataStore;
  plugin: ThinkPlugin;
}

export function Dashboard({ config, dataStore, plugin }: Props) {
  const [view, setView] = useState(config.initialView || 'Êúà');
  const [date, setDate] = useState(
    config.initialDate ? dayjs(config.initialDate) : dayjs(),
  );
  const [kw, setKw] = useState('');
  const [, force] = useState(0);

  /* Êï∞ÊçÆÂ±ÇÂèòÂä®ÂêéÂº∫Âà∂Âà∑Êñ∞ */
  useEffect(() => {
    const fn = () => force(v => v + 1);
    dataStore.subscribe(fn);
    return () => dataStore.unsubscribe(fn);
  }, [dataStore]);

  /* Â∞èÂ∑•ÂÖ∑ */
  const unit = (v: string) =>
    ({ Âπ¥: 'year', Â≠£: 'quarter', Êúà: 'month', Âë®: 'week', Â§©: 'day' } as any)[v] ??
    'day';
  const fmt = (d: any, v: string) =>
    v === 'Âπ¥'
      ? d.format('YYYYÂπ¥')
      : v === 'Â≠£'
      ? `${d.year()}Âπ¥${QTXT[d.quarter() - 1]}Â≠£Â∫¶`
      : v === 'Êúà'
      ? d.format('YYYY-MM')
      : v === 'Âë®'
      ? d.format('YYYY-[W]WW')
      : d.format('YYYY-MM-DD');

  const { startDate, endDate } = getDateRange(date, view);

  /* ---------------- Module Ê∏≤Êüì ---------------- */
  const renderModule = (m: ModuleConfig & { groups?: string[] }) => {
    const V = (ViewComponents as any)[m.view];
    if (!V)
      return (
        <ModulePanel module={m}>
          <div>Êú™Áü•ËßÜÂõæÔºö{m.view}</div>
        </ModulePanel>
      );

    /* ‚ë† ÈááÈõÜÂπ∂ËøáÊª§Êï∞ÊçÆ */
    let items = dataStore.queryItems();
    items = filterByRules(items, m.filters || []);
    items = filterByDateRange(
      items,
      startDate.format('YYYY-MM-DD'),
      endDate.format('YYYY-MM-DD'),
    );
    items = filterByKeyword(items, kw);

    /* ‚ë° ‰ª™Ë°®ÁõòÁ∫ß path / tag */
    if (config.tags?.length) {
      items = items.filter(it =>
        it.tags.some(t => config.tags!.some(x => t.includes(x))),
      );
    }
    if (config.path?.trim()) {
      const target = config.path.trim();
      const af = (dataStore as any)['app'].vault.getAbstractFileByPath(target);
      const keep = (p: string) =>
        af instanceof TFolder
          ? p.startsWith(target.endsWith('/') ? target : target + '/')
          : af instanceof TFile
          ? p.startsWith(target + '#')
          : p.startsWith(target.endsWith('/') ? target : target + '/');
      items = items.filter(it => keep(it.id));
    }

    /* ‚ë¢ ÊéíÂ∫è */
    items = sortItems(items, m.sort || []);

    /* ‚ë£ ËßÜÂõæ props ÊãºË£Ö ------------------------------------ */
    const vp: any = { ...(m.props || {}) };

    if ((m as any).groups?.length) vp.groupField = (m as any).groups[0];
    if (m.view === 'TableView') {
      vp.rowField = (m as any).rowField || '';
      vp.colField = (m as any).colField || '';
    }
    if (m.fields?.length) vp.fields = m.fields;

    return (
      <ModulePanel module={m}>
        <V items={items} {...vp} />
      </ModulePanel>
    );
  };

  /* ---------------- È°µÈù¢ ---------------- */
  return (
    <div>
      {/* È°∂Ê†è */}
      <div class="tp-toolbar" style="margin-bottom:8px;">
        {['Âπ¥', 'Â≠£', 'Êúà', 'Âë®', 'Â§©'].map(v => (
          <button onClick={() => setView(v)} class={v === view ? 'active' : ''}>
            {v}
          </button>
        ))}
        <button disabled style="font-weight:bold;margin:0 4px;background:#fff;">
          {fmt(date, view)}
        </button>
        <button onClick={() => setDate(date.clone().subtract(1, unit(view)))}>‚Üê</button>
        <button onClick={() => setDate(date.clone().add(1, unit(view)))}>‚Üí</button>
        <button onClick={() => setDate(dayjs())}>Ôºù</button>
        <input
          placeholder="Âø´ÈÄüËøáÊª§‚Ä¶"
          style="margin-left:4px;"
          value={kw}
          onInput={e => setKw((e.target as HTMLInputElement).value)}
        />
        <button
          style="margin-left:4px;"
          onClick={() => plugin.openSettingsForDashboard(config.name)}
          title="Âú®Êèí‰ª∂ËÆæÁΩÆ‰∏≠ÁºñËæëÊú¨‰ª™Ë°®Áõò"
        >
          ‚öôÔ∏é
        </button>
      </div>

      {/* Ê®°ÂùóÂæ™ÁéØ */}
      {config.modules.map(renderModule)}
    </div>
  );
}
